
html{
    height: 100%;
    padding: 0;
    margin: 0;
}
body{
    height: 100%;
    padding: 0;
    margin: 0;
}

.row{
    height: 100%;
    padding: 0;
    margin: 0;
}
.sidebar{
    width: 25%;
    float: left;
    height: 100%;
    background-image: url(https://www.andreasreiterer.at/wp-content/uploads/2017/08/cropped-cropped-orig_647250-2.jpg);
    position: fixed;
}
.content{
    width: 75%;
    height: 100%;
    float: right;
    background-color: white;
    margin-left: 470px;
}
.header{
    margin: 0;
    padding: 12px;
    text-align: center;
    background-color: #FFFFFF;
    box-shadow: 0 0 8px 2px rgba(0,0,0,.2);
    position: fixed;
    width: 75%;
    right: 0;
    box-sizing: border-box;
    top: 0;
    z-index: 1;
}
.header ul{
    padding: 0;
    margin: 0;
    list-style: none;
}
.header ul li{
    padding: 0;
    margin: 10px;
    font-size: 25px;
    display: inline-block;
    font-family:'viga-regular';
    position: relative;
}
.linksdiv{
    width: 58%;
    margin-top: 50px;
    margin-left: 86px;
}
.nav-menu li{
    padding: 0;
    margin: 5px;
    font-size: 25px;
    font-family:'viga-regular';
    position: relative;
    list-style: none;
    color: black;
    text-align: right;
    border-bottom: 1px solid white; 
}
.nav-menu li a{
   color : white;
}
.header ul li a{
    color: black;
    text-decoration: none;
}
.header ul li:hover{
    color: red;
}

.header ul li:after{
    content: "";
    width: 0;
    height: 4px;
    position: absolute;
    left: 0;
    bottom: 0;
    background: #2a2f4a;
    transition: all 400ms;  
}
.header ul li.active:after{
    width: 100%;
}
.header ul li:hover:after{
    width: 100%;
}

.header ul li::selection{
    background-color: aqua;
    color: red;
}
