}
.about{
    padding:40px 50px;
    margin: 0 auto;
    position: relative;
}
.lftBorder{
    width: 2px;
    height: 100%;
    padding: 0;
    margin: 0;
    position: absolute;
    left: 30px;
    top: 0;
    background-color: #000;
}
.lftBorder:after,
.lftBorder:before{
    content: '';
    width:26px;
    height:2px;
    padding: 0;
    margin: 0;
    position: absolute;
    left:-12px;
    top: 0;
    background-color: #2A2F4A;
}
.lftBorder:before{
    top:auto;
    bottom: 0;
}
.aboutme{
    color: black;
    font-family:'viga-regular';
    font-size: 20px;
    line-height: 30px;
    text-align: center;
    padding: 0 20px 0;
}
.about p{
    color: black;
    font-family:'viga-regular';
    font-size: 20px;
    line-height: 30px;
}
.aboutCont{
    padding:0;
    margin:0;
    position: relative;
}
.aboutCont:before{
    content: '';
    width:26px;
    height:2px;
    padding: 0;
    margin: 0;
    position: absolute;
    left:-22px;
    top:19px;
    background-color: #2A2F4A;
}
.aboutCont:after {
    content: '';
    width: 12px;
    height: 12px;
    padding: 0;
    margin: 0;
    position: absolute;
    left: -26px;
    top: 13px;
    border: 1px solid #000;
    border-radius: 50%;
    background-color: #fff;
}
.aboutHdr{
